.hero {
    //grid-gap: 32px;
    background: $c-lightgray;
    height: 510px;
    overflow: hidden;

    .button {
        margin-right: 16px;
    }

    .hero__illustration-container {
        width: 55%;
        height: 525px;
        float: left;
        position: relative;

        .hero__image {
            height: 525px;
            width: 525px;
            top: 120px;
            right: 32px;
            position: absolute;
            background-image: url("../images/portfolio-illustration.png");
            background-size: contain;
            background-repeat: no-repeat;
            background-position: bottom;
            max-width: 525px;
        }
    }

    .hero__text-container {
        display: inline-block;
        width: 45%;
        margin-top: 50px;

        h1 {
            font-size: $f-size-lg2;
            font-weight: bold;
            padding: 16px 0;
        }
        p {
            font-size: $f-size-md2;
            line-height: 1.4;
            padding: 16px 0;
        }
    }
    
    

}