.projects__container {
    background: $c-purple;

    &:after {
        content: "";
        width: 100%;
        height: 100px;
        background:url("../images/projects-vector.png");
        left: 0;
        bottom: 0;
        position: absolute;
        display: inline-block;
    }
}

.projects {
    h1 {
        font-weight: bold;
        font-size: $f-size-lg2;
        color: white;
        padding: 32px 0;
    }
    .project {
        background: $c-white;
        border-radius: 4px;
    }

}