.navbar__container {
    width: 100%;
}
.navbar {
    height: 148px;
    display: grid;
    grid-template-columns: 1px auto;
    z-index: 2;

    .navbar__logo {
        margin: auto auto auto 0;
        padding: 20px;
    }
    ul {
        margin: auto 0 auto auto;
    }
    ul > li {
        display: inline-block;
        padding: 20px;
        font-size: $f-size-md2;
    }
}
